import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    sidebarIsOpen: false,
  },
  mutations: {
    toggleSidebar(state) {
      state.sidebarIsOpen = !state.sidebarIsOpen;
    },
    closeSidebar(state) {
      state.sidebarIsOpen = false;
    },
  },
  getters: {
    sidebarIsOpen(state) {
      return state.sidebarIsOpen;
    },
  },
  actions: {
  },
  modules: {
  },
});
