<template>
  <button id="theme-switcher" class="header-theme-switcher" @click="switchTheme">
    Сменить тему
  </button>
</template>

<script>
export default {
  name: 'HeaderThemeSwitcher',
  methods: {
    switchTheme() {
      const root = document.documentElement;
      const attrName = 'data-dark-theme';
      let attrValue;

      if (root.getAttribute(attrName) === 'true') {
        attrValue = 'false';
      } else {
        attrValue = 'true';
      }

      root.setAttribute(attrName, attrValue);

      localStorage.darkTheme = attrValue;
    },
  },
};
</script>

<style scoped>
.header-theme-switcher {
  border: none;
  background-color: transparent;
  cursor: pointer;
  height: 100%;
  box-sizing: border-box;
  color: var(--color);
  max-width: 150px;
  margin-left: auto;
  display: block;
}

.header-theme-switcher:hover {
  background-color: var(--hover-background-color);
}
</style>
