<template>
  <aside class="sidebar" v-click-outside="clickOutside">
    <sidebar-links/>
  </aside>
</template>

<script>
import vClickOutside from 'v-click-outside';
import { mapMutations } from 'vuex';
import SidebarLinks from './the-sidebar/SidebarLinks.vue';

export default {
  name: 'TheSidebar',
  directives: {
    clickOutside: vClickOutside.directive,
  },
  components: {
    SidebarLinks,
  },
  methods: {
    ...mapMutations(['closeSidebar']),
    clickOutside({ target }) {
      if (target.id !== 'menu' && target.parentElement.id !== 'menu' && target.id !== 'theme-switcher') {
        this.closeSidebar();
      }
    },
  },
};
</script>

<style scoped>
.sidebar {
  height: 100vh;
  overflow-y: auto;
}
</style>
